import * as React from "react";
import MenuButton from "./menuButton";

class MobileNav extends React.Component {

  const
  menu = false;

  constructor(props) {
    super(props);
    this.menu = false;
  }

  toggleMenu() {
    this.menu = true;
  }

  render() {
    let menu = undefined;
    if (this.menu) {
      menu = (<div>
        <h1>Show</h1>
      </div>);
    }
    return (
        <div>
          <MenuButton onClick={this.toggleMenu}/>
          {menu}
        </div>
    );
  }
}

export default MobileNav;
